import React from "react";
import Back from "./Back";
import WithBackground from "./WithBackground";
const TopNav = (props) => {
  let html = "";
  let { variant, title, click } = props;
  switch (variant) {
    case "with-background":
      html = <WithBackground {...props} />;
      break;
    default:
      html = <Back {...props} />;

      break;
  }
  return <>{html}</>;
};

export default TopNav;
